import React from "react";
import Styled from '@emotion/styled';

import { NavBar } from './NavBar';
import { ResumeWrapper, MainContent, HeaderWrapper, Dates, Header, SubHeader, ResumeEntryWrapper, Description, SectionTitle } from './ResumeStyledComponents';

const resumeObject = {
    education: [
        { header: 'University of Texas at Dallas', subHeader: 'Bachelors of Science in Computer Science', dates: '2022-2025', description: 'Gained proficieny in coding in several langauges such as C++, Java, and python. Knowledge in advanced systems, data structures, and technical theory.' },
    ],
    work: [
        { header: 'Bear Cave Coffee', subHeader: 'Barista', dates: 'August 2019 - May 2020', description: 'Collaborate efficiently on a team, ensure customer care, and run coffee shop smoothly and efficiently.' },
        { header: 'Bear Cave Coffee', subHeader: 'Managerial Barista', dates: 'May 2020 - Now', description: 'Engage skills in leadership and collaboration, ensure employees and customers are looked after.' },
    ]
};

const ResumeEntry = ({ header = '', subHeader = '', dates = '', description = '' }) => (
    <ResumeEntryWrapper>
        <HeaderWrapper>
            <Header>{header}</Header>
            <Dates>{dates}</Dates>
        </HeaderWrapper>
        <SubHeader>{subHeader}</SubHeader>
        <Description>{description}</Description>
    </ResumeEntryWrapper>
);

const resumeEntryBuilder = (entries) => (
    entries.map(entry =>
        <ResumeEntry header={entry.header} subHeader={entry.subHeader} dates={entry.dates} description={entry.description} />
    )
)



export const Resume = () => (
    <ResumeWrapper>
        <NavBar />
        <MainContent>
            <SectionTitle>EDUCATION</SectionTitle>
            {resumeEntryBuilder(resumeObject.education)}
            <SectionTitle>WORK</SectionTitle>
            {resumeEntryBuilder(resumeObject.work)}
        </MainContent>
    </ResumeWrapper>
);